import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { SharedComponents } from "src/app/shared.components";
import { IPort } from "src/interfaces/i.port.interface";
import { FOPModel } from "src/models/fop/fop.model";
import { AlertService } from "src/services/alert.service";
import { CurrentUserService } from "src/services/authentcation/current.user.service";
import { HttpService } from "src/services/http/http.service";
import { PortService } from "src/services/port.service";
import { SEOService } from "src/services/seo.service";

@Component({
  selector: 'app-setting',
  templateUrl: './ports.component.html',
  styles: [
  ]
})
/**
 * This a general controller 
 * 
 */
export class PortsComponent extends SharedComponents implements OnInit {
  /**
   * Page Target
   */
  skip: number = 0;
  limit: number = 50;
  entries: IPort[];
  fop: FOPModel = new FOPModel();
  fopSearch: string;
  constructor(public service: PortService, private activateRouter: ActivatedRoute) { super() }

  ngOnInit(): void {
    //Listener On Parems
    this.allSubscriptions.push(
      this.activateRouter.params.subscribe(parms => {
        this.service.updatePortTypeInformation(this.activateRouter.snapshot.data.portType);
        this.entries = [];
        this.skip = 0
        this.fop = new FOPModel();
        this.getEntiries();
      })

    );
    //Listener On Delete Item Event
    this.allSubscriptions.push(
      this.service.onDeletedItem.subscribe(deletedItemId => {
        let deletedIndex = this.entries.findIndex(ent => ent._id == deletedItemId);
        if (deletedIndex >= 0) {
          this.entries.splice(deletedIndex, 1);
          this.fop = new FOPModel(this.fop.fieldNames, this.entries.length);
        }
      }));

  }


  /**
 * Get My Users
 */
  getEntiries(isFilter: boolean = false): void {
    this.fop.paging.isRequestingToServer = true;

    //Get Users Now From Server
    this.allSubscriptions.push(
      this.service.http.getPorts<IPort[]>(this.skip, this.limit,this.service.portType).subscribe(res => {
        //Update FOP Properties
        if (res.noMoreOfResult)
          this.fop.paging.disableGetMoreButton = true;
        this.fop.paging.isRequestingToServer = false;

        if (res.isSuccess) {
          this.entries = [...this.entries, ...res.result];

          //Fop 
          this.fop = new FOPModel(this.fop.fieldNames, this.entries.length);
          this.skip += this.limit;
        } else {
          AlertService.showResponseMessage(res);
        }
      }, error => {
        AlertService.canRequestError(error);
        this.fop.paging.isRequestingToServer = false;
      }, () => {
        this.fop.paging.isRequestingToServer = false;
      }));
  }


  funTrack(index: number, entitiy: IPort): string | undefined {
    return entitiy?._id;
  }
}//End Class
