import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { AppSettingComponent } from "src/app/back-end/settings/app-setting/app.setting.component";
import { CreateGeneralSettingComponent } from "src/app/back-end/settings/general-setting/create-general-setting/create.general.setting.component";
import { DeleteGeneralSettingComponent } from "src/app/back-end/settings/general-setting/delete-general-setting/delete.general.setting.component";
import { EditGeneralSettingComponent } from "src/app/back-end/settings/general-setting/edit-general-setting/edit.general.setting.component";
import { GeneralSettingComponent } from "src/app/back-end/settings/general-setting/general.setting.component";
import { CreatePortComponent } from "src/app/back-end/settings/ports/create-port/create.port.component";
import { DeletePortComponent } from "src/app/back-end/settings/ports/delete-port/delete.port.component";
import { EditPortComponent } from "src/app/back-end/settings/ports/edit-port/edit.port.component";
import { PortsComponent } from "src/app/back-end/settings/ports/ports.component";
import { CreateStateComponent } from "src/app/back-end/settings/states/create-state/create.state.component";
import { DeleteStateComponent } from "src/app/back-end/settings/states/delete-state/delete.state.component";
import { EditStateComponent } from "src/app/back-end/settings/states/edit-state/edit-state.component";
import { StatesComponent } from "src/app/back-end/settings/states/states.component";
import { PortTypesEnum } from "src/enums/port.types.enum";

const ROUTS: Routes = [
    {
        path: 'app', component: AppSettingComponent
    },
    {
        path: 'states', component: StatesComponent,
        children: [
            {
                path: "delete/:targetId",
                component: DeleteStateComponent,
            }
        ]
    },
    { path: 'states/create', component: CreateStateComponent },
    { path: 'states/edit/:targetId', component: EditStateComponent },

    //Ports Freight
    {
        path: 'ports/freight', component: PortsComponent, data: { portType: PortTypesEnum.freight },
        children: [{ path: "delete/:targetId", component: DeletePortComponent, data: { portType: PortTypesEnum.freight } },]
    },
    { path: "ports/freight/edit/:targetId", component: EditPortComponent, data: { portType: PortTypesEnum.freight } },
    {
        path: "ports/freight/create",
        component: CreatePortComponent,
        data: { portType: PortTypesEnum.freight }
    },
    //Ports Arrival
    {
        path: 'ports/arrival', component: PortsComponent, data: { portType: PortTypesEnum.arrival },
        children: [{ path: "delete/:targetId", component: DeletePortComponent, data: { portType: PortTypesEnum.arrival } }]
    },
    {
        path: "ports/arrival/edit/:targetId",
        component: EditPortComponent,
        data: { portType: PortTypesEnum.arrival }
    },
    {
        path: "ports/arrival/create",
        component: CreatePortComponent,
        data: { portType: PortTypesEnum.arrival }
    },

    //General Setting
    {
        path: ':pageTargetName', component: GeneralSettingComponent,
        children: [
            {
                path: "create",
                component: CreateGeneralSettingComponent
            },
            {
                path: "edit/:targetId",
                component: EditGeneralSettingComponent,
            },
            {
                path: "delete/:targetId",
                component: DeleteGeneralSettingComponent,
            }
        ]
    },
];

@NgModule({
    imports: [RouterModule.forChild(ROUTS)],
    exports: [RouterModule]
})
export class SettingsRoutingModule {

}

