import { PortTypesEnum } from 'src/enums/port.types.enum';
import { EventEmitter, Injectable } from "@angular/core";
import { FormControl, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { CurrentUserService } from "./authentcation/current.user.service";
import { HttpService } from "./http/http.service";
import { SEOService } from "./seo.service";
import { CustomValidatorsService } from './custom.validators.service';
import { flatten } from '@angular/compiler';
import { SweetAlertService } from './sweet.alert.service';

@Injectable()
export class PortService {

    /**   */
    portType: PortTypesEnum;
    portTypeName: string = "";
    form: FormGroup = new FormGroup({});
    isFormSubmitError: boolean = false;
    isRequestingToServer: boolean = false;
    portTypeIsFreight: boolean = false;

    /** 
     * Current Page Target
     */
    currentPageTargetName: string;

    /** Deleted Item Event*/
    onDeletedItem: EventEmitter<string> = new EventEmitter<string>();

    constructor(public u: CurrentUserService, public http: HttpService, public router: Router,
        public seo: SEOService, public sweetAlertService: SweetAlertService) { }
    /**
     * Close: Back To The Page
     */
    public closeModel() {

        this.router.navigate(['/setting', "ports", this.portTypeName]);
    }

    /**
     * Init Form To Create
     */
    initCreateUpdateForm() {
        //Check If Action On Port Freight
        if (this.portTypeIsFreight) {
            this.form = new FormGroup({
                _id: new FormControl(null),
                name: new FormControl(null, [Validators.required]),
                portType: new FormControl(this.portType, [Validators.required]),
                maritimeFreightPriceToBigCar: new FormControl(null, [Validators.required, CustomValidatorsService.invalidNumber]),
                maritimeFreightPriceToSmallCar: new FormControl(null, [Validators.required, CustomValidatorsService.invalidNumber]),
                maritimeFreightPriceToMediumCar: new FormControl(null, [Validators.required, CustomValidatorsService.invalidNumber]),
                backgroundColor: new FormControl(null, [Validators.required]),
                fontColor: new FormControl(null, [Validators.required]),
            });
        } else {
            this.form = new FormGroup({
                _id: new FormControl(null),
                name: new FormControl(null, [Validators.required]),
                portType: new FormControl(this.portType, [Validators.required]),
            });
        }
        this.isFormSubmitError = false;
        this.isRequestingToServer = false;
    }

    /**
     * Init Delete Form
     */
    initDeleteForm() {
        this.form = new FormGroup({
            _id: new FormControl(null, Validators.required),
            name: new FormControl(null, Validators.required)
        });
        this.form.disable();
        this.isFormSubmitError = false;
        this.isRequestingToServer = false;
    }

    /**
     * Update Port Type Target Information
     */
    updatePortTypeInformation(portType: PortTypesEnum) {
        this.portType = portType;
        switch (this.portType) {
            case PortTypesEnum.arrival:
                {
                    this.portTypeIsFreight = false;
                    this.portTypeName = "arrival";
                    this.currentPageTargetName = "arrivalPorts";
                }
                break;
            case PortTypesEnum.freight:
                {
                    this.portTypeIsFreight = true;
                    this.portTypeName = "freight";
                    this.currentPageTargetName = "freightPorts";
                }
                break;
        }
        this.updateSeoPage();
    }

    /** Update Page SEO */
    updateSeoPage(pageName: string = this.currentPageTargetName) {
        this.currentPageTargetName = pageName;
        this.seo.updateSEO(pageName);
    }

}//End Class