import { CustomValidatorsService } from './custom.validators.service';
import { ICity } from './../interfaces/i.city.interface';
import { Router } from '@angular/router';
import { EventEmitter, Injectable } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormControlName, FormGroup, Validators } from '@angular/forms';
import { IPort } from 'src/interfaces/i.port.interface';
import { AlertService } from './alert.service';
import { PortTypesEnum } from 'src/enums/port.types.enum';
import { HttpService } from './http/http.service';
@Injectable()
export class StateService {

  formSubmitError: boolean = false;
  isRequestingToServer: boolean = false;
  formCiteisSubmitError: boolean = false;
  citties: ICity[] = [];
  freightPorts: IPort[];

  form: FormGroup = new FormGroup({});
  formCities: FormGroup = new FormGroup({})

  /** Deleted Item Event*/
  onDeletedItem: EventEmitter<string> = new EventEmitter<string>();

  constructor(private router: Router, private builder: FormBuilder,public http:HttpService) { }

  /** Init Form */
  initForm() {
    this.form = new FormGroup({
      _id: new FormControl(null),
      name: new FormControl(null, [Validators.required]),
      freightPortId: new FormControl(null, [Validators.required]),
    });

    this.formCities = new FormGroup({
      _id: new FormControl(null),
      name: new FormControl(null, [Validators.required]),
      landFreightPrice: new FormControl(null, CustomValidatorsService.invalidNumber),
      listOfCities: new FormArray([])
    });
  }

  get listOfCitiesControl() {
    return (this.formCities.get('listOfCities') as FormArray).controls;
  }

  /**
   * Close: Back To The Page
   */
  public closeModel() {
    this.formSubmitError = false;
    this.formCiteisSubmitError = false;
    this.form.reset();
    this.router.navigate(['/setting', 'states']);
  }

  /**
   * Add New City
   */
  addNewCity(): void {
    if (this.formCities.invalid) {
      this.formCiteisSubmitError = true;
      return;
    }
    (<FormArray>this.formCities.get('listOfCities'))
      .push(
        this.builder.group({
          _id: new FormControl(null),
          name: new FormControl(this.formCities.controls.name.value, [Validators.required]),
          landFreightPrice: new FormControl(this.formCities.controls.landFreightPrice.value, CustomValidatorsService.invalidNumber),
          IsDeleting: false
        }));
    this.formCities.controls.name.setValue(null);
    this.formCities.controls.landFreightPrice.setValue(null);
    this.formCiteisSubmitError = false;
  }

  /**
   * Add New City To Update
   */
  addNewCityV2(cities: ICity[]): void {
    let fArray = (<FormArray>this.formCities.get('listOfCities'));
    cities.forEach(ci =>
      fArray.push(
        this.builder.group({
          _id: ci._id,
          name: ci.name,
          landFreightPrice: + ci.landFreightPrice,
          IsDeleting: false,
        }))
    );
  }

  /** Remove City */
  removeCity(index: number) {
    let formArray = (this.formCities.get('listOfCities') as FormArray);
    let deletingObject = formArray.controls[index]
    //Check If This Is Update Process
    if (!this.form.controls._id.value || !deletingObject.value._id) {
      //Remove In Create Case
      formArray.removeAt(index)
    } else {
      deletingObject.patchValue({
        _id: deletingObject.value._id,
        name: deletingObject.value.name,
        landFreightPrice: + deletingObject.value.landFreightPrice,
        IsDeleting: true
      });
      deletingObject.disable();
    }
  }

  /**Get Items To Select From Them */
  getItems(itemName?: string): void {

    this.http.getSelectItems<any>(["freightPorts"]).subscribe(res => {
      if (res.isSuccess) {
        /**             */
        this.freightPorts = res.result.freightPorts
      } else {
        AlertService.showResponseMessage(res);
      }
    }, error => {
      AlertService.canRequestError(error);
    })
  }

  //Reload Items
  reloadItems(itemName: string): void {
    this.http.getSelectItems<any>(
      [itemName]).subscribe(res => {
        if (res.isSuccess) {
          /**             */
          this.freightPorts = res.result[itemName]
        }
        AlertService.showResponseMessage(res);
      }, error => {
        AlertService.canRequestError(error);
      })
  }
}//End Class