import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { SharedComponents } from "src/app/shared.components";
import { IPort } from "src/interfaces/i.port.interface";
import { AlertService } from "src/services/alert.service";
import { PortService } from "src/services/port.service";

@Component({
  selector: 'app-delete-general-setting',
  templateUrl: './delete.port.component.html',
  styles: [
  ]
})
export class DeletePortComponent extends SharedComponents implements OnInit {
  constructor(public service: PortService,private activateRouter:ActivatedRoute) { super() }

  ngOnInit(): void {
    if (!this.activateRouter.snapshot.params.targetId) {
      this.service.closeModel();
      return;
    }
    this.service.initDeleteForm();


    //Check If Must Be Get Object From The Server
    if (history.state.deletingItemName) {
      this.service.form.controls.name.setValue(history.state.deletingItemName);
      history.state.deletingItemName = null;
    }
    else {
      //Get From The Server To Delete
      this.getPortToDelete();
    }
  }

  /**
   * Get Genral Setting From Server To Delete
   */
  getPortToDelete() {
    this.service.isRequestingToServer = true;
    this.allSubscriptions.push(
      this.service.http.getPortToDelete<IPort>(this.activateRouter.snapshot.params.targetId)
        .subscribe(res => {
          this.service.isRequestingToServer = false;
          if (res.isSuccess) {
            this.service.form.controls.name.setValue(res.result.name);
          }
        }, error => {
          this.service.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }

  /**
   * Delete Item
   */
  onSubmit() {
    if (this.service.form.invalid) {
      this.service.isFormSubmitError = true;
      return;
    }
    this.service.isRequestingToServer = true;
    this.allSubscriptions.push(
      this.service.http.deletePort<IPort>(this.activateRouter.snapshot.params.targetId)
        .subscribe(res => {
          AlertService.showResponseMessage(res);
          this.service.isRequestingToServer = false;
          if (res.isSuccess) {
            //Pass The New Item To Page
            this.service.onDeletedItem.emit(this.activateRouter.snapshot.params.targetId);
            //Back To Component
            this.service.closeModel();
          }
        }, error => {
          this.service.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }

}//End Class
