import { Component, OnInit } from "@angular/core";
import { FormControl, FormGroup, Validators } from "@angular/forms";
import { SharedComponents } from "src/app/shared.components";
import { IAppSetting } from "src/interfaces/i.app.setting";
import { AlertService } from "src/services/alert.service";
import { CurrentUserService } from "src/services/authentcation/current.user.service";
import { CustomValidatorsService } from "src/services/custom.validators.service";
import { HttpService } from "src/services/http/http.service";
import { SEOService } from "src/services/seo.service";



@Component({
    selector: "app-config",
    templateUrl: "./app.setting.component.html"
})
export class AppSettingComponent extends SharedComponents implements OnInit {
    form: FormGroup;
    isFormSubmitError: boolean;
    isRequestingToServer: boolean;
    constructor(public u: CurrentUserService, public http: HttpService,private seo:SEOService) {
        super();
    }


    ngOnInit() {
        this.seo.updateSEO("appSetting");
        this.form = new FormGroup({
            _id: new FormControl(null, Validators.required),
            nextRefranceNumber: new FormControl(null, [Validators.required, Validators.min(1), CustomValidatorsService.invalidNumber])
        });
        this.getAppSettingValues();
    }

    /**
     * Get AppSetting Values
     */
    getAppSettingValues() {
        this.http.getAppSettingValues<IAppSetting>().subscribe(res => {
            this.isRequestingToServer = false;
            if (res.isSuccess) {
                debugger
                this.form.setValue(res.result);
            }
        }, error => {
            this.isRequestingToServer = false;
            AlertService.canRequestError(error);
        });
    }


    /** On Form Submit */
    onSubmit() {
        if (this.form.invalid) {
            this.isFormSubmitError = true;
            AlertService.errorMessage(this.u.r.youHaveSomeErrors);
            return;
        }

        this.isRequestingToServer = true;
        this.isFormSubmitError = false;
        this.form.disable();
        this.allSubscriptions.push(
            this.http.updateAppSettingValues<IAppSetting>(this.form.value)
                .subscribe(res => {
                    AlertService.showResponseMessage(res);
                    this.form.enable();
                        this.isRequestingToServer = false;
                }, error => {
                    this.form.enable();
                    this.isRequestingToServer = false;
                    AlertService.canRequestError(error);
                }));
    }
}//End Class