import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { SharedComponents } from "src/app/shared.components";
import { IPort } from "src/interfaces/i.port.interface";
import { AlertService } from "src/services/alert.service";
import { PortService } from "src/services/port.service";

@Component({
  selector: 'app-edit-general-setting',
  templateUrl: '../create-port/create.port.component.html',
  styles: [
  ]
})
export class EditPortComponent extends SharedComponents implements OnInit {

  constructor(public service: PortService, private activatedRoute: ActivatedRoute) { super() }

  ngOnInit(): void {
    this.service.updatePortTypeInformation(this.activatedRoute.snapshot.data.portType);
    this.service.initCreateUpdateForm();
    this.service.updateSeoPage("editPort");

    //Get From The Serer
    this.getPortToUpdate();
  }

  /**
   * Get Genral Setting From Server To Update
   */
  getPortToUpdate() {
    this.allSubscriptions.push(
      this.service.http.getPortToUpdate<IPort>(this.activatedRoute.snapshot.params.targetId)
        .subscribe(res => {
          debugger
          if (res.isSuccess) {
            if (this.service.portTypeIsFreight) {
              this.service.form.setValue(res.result);

            } else {
              this.service.form.setValue({
                _id: res.result._id,
                name: res.result.name,
                portType: res.result.portType,
              });
            }
          }
        }, error => {
          AlertService.canRequestError(error);
        }));
  }

  /**
   * Update Item
   */
  onSubmit() {
    if (this.service.form.invalid) {
      this.service.isFormSubmitError = true;
      return;
    }
    this.service.isRequestingToServer = true;
    this.service.form.disable();
    this.allSubscriptions.push(
      this.service.http.updatePort<IPort>(this.service.form.value)
        .subscribe(res => {
          AlertService.showResponseMessage(res);
          this.service.isRequestingToServer = false;
          if (res.isSuccess) {
            this.service.form.enable();
          }
        }, error => {
          AlertService.canRequestError(error);
        }));
  }

}//End Class
